    font-size: 1.38rem;
    font-weight: normal ; }
    header h1 a,
    header h2 a {
      padding-bottom: 0.25rem ;
      padding-top: 0.25rem ;
      text-decoration: none ;
      color: black ; }
      header h1 a:hover,
      header h2 a:hover {
        border-bottom-width: 2px;
        border-bottom-style: solid;
        border-bottom-color: #0050d8 ; }

